import React from 'react';

const Register = React.lazy(() => import('./Pages/Register'));
const Dashboard = React.lazy(() => import('./Pages/Dashboard'));
const Myprofile = React.lazy(()=> import('./Pages/MyProfile'));
const Wallet = React.lazy(()=> import('./Pages/Wallet'));
const AddOrder = React.lazy(()=> import('./Pages/AddOrder'));
const MyCashback = React.lazy(()=> import('./Pages/MyCashback'));
const MyOldCashback = React.lazy(()=> import('./Pages/MyOldCashback'));
const ChangeLanguage = React.lazy(()=> import('../CommonRoutes/ChangeLanguage'));
const SignOut= React.lazy(()=> import('../CommonRoutes/SignOut'));
const Terms= React.lazy(()=> import('../CommonRoutes/TermsCondition'));
const PrivacyPolicy= React.lazy(()=> import('../CommonRoutes/PrivacyPolicy'));
const ContactUs = React.lazy(()=> import('../CommonRoutes/ContactUs'));
const AboutUs = React.lazy(()=> import('../CommonRoutes/AboutUs'));
const UpdateProfile=React.lazy(()=> import('./Pages/UpdateProfile'));
const Bankcard=React.lazy(()=> import('./Pages/Bankcard'));
const ForgotPass=React.lazy(()=> import('../CommonRoutes/ForgotPass'));
const Location=React.lazy(()=> import('./Pages/Location'));
const ChangePassword=React.lazy(()=> import('../CommonRoutes/ChangePassword'));
const NearVendor=React.lazy(()=> import('./Pages/NearVendor'));
const CategorySlide=React.lazy(()=> import('./Pages/CategorySlide'));
const CategorySlide2=React.lazy(()=> import('./Pages/CategorySlide2'));
const ViewAll=React.lazy(()=> import('./Pages/ViewAll'));
const ViewAllProduct=React.lazy(()=> import('./Pages/ViewAllProduct'));
const SearchVendor=React.lazy(()=> import('./Pages/SearchVendor'));
const Qrcode = React.lazy(() => import('./Pages/Qrcode'));


const userRoutes = [
  // { path: '/', exact: true, name: 'Home' }, 

  { path: '/Register', name: 'Register', component: Register },
  { path: '/dashboard', name: 'Dashboard', component: Dashboard },
  { path: '/myprofile', name: 'Myprofile', component: Myprofile, exact: true },
  { path: '/wallet', name: 'Wallet', component: Wallet, exact: true },
  { path: '/addorder', name: 'AddOrder', component: AddOrder, exact: true },
  { path: '/mycashback', name: 'MyCashback', component: MyCashback, exact: true },
  { path: '/myoldcashback', name: 'MyOldCashback', component: MyOldCashback, exact: true },
  { path: '/setting/changelanguage', name: 'ChangeLanguage', component:ChangeLanguage , exact: true },
  { path: '/setting/signout', name: 'SignOut', component:SignOut , exact: true },
  { path: '/setting/terms', name: 'Terms', component:Terms , exact: true },
  { path: '/setting/privacypolicy', name: 'privacypolicy', component:PrivacyPolicy , exact: true },
  { path: '/contactus', name: 'ContactUs', component:ContactUs , exact: true },
  { path: '/AboutUs', name: 'AboutUs', component:AboutUs , exact: true },
  { path: '/userupdateprofile', name: 'UpdateProfile', component:UpdateProfile  },
  { path: '/bankcard', name: 'BankCard', component:Bankcard  },
  { path: '/forgotpass', name: 'ForgotPass', component:ForgotPass },
  { path: '/location', name: 'Location', component:Location },
  { path: '/ChanngePassword', name: 'ChangePassword', component:ChangePassword },
  { path: '/near', name: 'NearVendor', component:NearVendor },
  { path: '/cat/:id', name: 'categorySlide', component:CategorySlide },
  { path: '/catevendor/:id', name: 'categorySlide2', component:CategorySlide2 },
  { path: '/viewall', name: 'viewall', component:ViewAll },
  { path: '/viewallproduct', name: 'viewallProduct', component:ViewAllProduct },
  { path: '/searchvendor/:id', name: 'SearchVendor', component:SearchVendor },
  { path: '/qrcode', name: 'Qrcode', component: Qrcode },
  
];

export default userRoutes;
