import React from 'react'
import { useSelector, useDispatch } from 'react-redux'
import {
  CCreateElement,
  CSidebar,
  CSidebarBrand,
  CSidebarNav,
  CSidebarNavDivider,
  CSidebarNavTitle,
  CSidebarMinimizer,
  CSidebarNavDropdown,
  CSidebarNavItem,
  CHeaderBrand,
} from '@coreui/react'
import Avtar from '../../src/assets/img/avtar.png';
import '../scss/profile.scss'

// sidebar nav config
import navigationUser from './_navUser'
import navigationVendor from './_navVendor'

const TheSidebar = () => {
  const dispatch = useDispatch()
  const show = useSelector(state => state.sidebarShow)
  const role=localStorage.getItem("role")

    if(role === "vendor"){
      return(
    <CSidebar  style={{backgroundColor:'#202342'}}
      show={show}
      onShowChange={(val) => dispatch({type: 'set', sidebarShow: val })}
    >
      <CSidebarBrand className="d-md-down-none">
      <div className="Side-picture-container">
          <div className="picture1">
              <img src={Avtar} style={{ width: '60px', height: '60px' }} alt="" />
          </div>
                                            
      </div>
      </CSidebarBrand>
      <CSidebarNav >
      <CHeaderBrand className="mx-auto d-lg-none">
      <div className="Side-picture-container">
          <div className="picture1">
              <img src={Avtar} style={{ width: '60px', height: '60px' }} alt="" />
          </div>
                                            
      </div>
      </CHeaderBrand>

        <CCreateElement  style={{color:"black"}}
          items={navigationVendor}
          components={{
            CSidebarNavDivider,
            CSidebarNavDropdown,
            CSidebarNavItem,
            CSidebarNavTitle 
          }}
        />
      </CSidebarNav>
      
      <CSidebarMinimizer className="c-d-md-down-none"/>
    </CSidebar>)}
    else{
      return(
        <CSidebar 
        
      show={show}
      onShowChange={(val) => dispatch({type: 'set', sidebarShow: val })}
    >
       <CSidebarBrand className="d-md-down-none">
      <div className="Side-picture-container">
          <div className="picture1">
              <img src={Avtar} style={{ width: '60px', height: '60px' }} alt=""/>
          </div>
                                            
      </div>
      </CSidebarBrand>
      <CSidebarNav >
      <CHeaderBrand className="mx-auto d-lg-none">
      <div className="Side-picture-container">
          <div className="picture1">
              <img src={Avtar} style={{ width: '60px', height: '60px' }} alt=""/>
          </div>
                                            
      </div>
      </CHeaderBrand>

        <CCreateElement  style={{color:"black"}}
          items={navigationUser}
          components={{
            CSidebarNavDivider,
            CSidebarNavDropdown,
            CSidebarNavItem,
            CSidebarNavTitle 
          
          }}
        />
      </CSidebarNav>
      <CSidebarMinimizer className="c-d-md-down-none"/>
    </CSidebar>
      )
    }
}
export default React.memo(TheSidebar)
